<template>
  <section class="main">
    <loader class="main__loader" v-if="!isLoaded"></loader>
    <template v-else>
      <div class="main__content main__content_a">
        <back-link
          v-if="isRepo"
          :to="`/repos/${repoId}`"
          class="back-link">{{ repoName }}</back-link>
        <div class="section heading">
          <div
            class="heading__icon"
            :class="{ heading__icon_vertical: type === 'books' }"
            :style="`background-image: url(${app.image})`"
          ></div>
          <div
            ref="desc"
            class="heading__desc"
            :class="{
              heading__desc_expanded: isDescExpanded,
              heading__desc_book: type === 'books',
            }"
          >
            <div class="heading__mobile">
              <div
                class="heading__icon-mobile"
                :class="{ 'heading__icon-mobile_vertical': type === 'books' }"
                :style="`background-image: url(${app.image})`"
              ></div>
              <h1 class="heading__title">
                <span class="heading__title-inner">{{ app.name }}</span
                ><span class="heading__badge" v-if="isTweaked">{{
                  $t("TWEAKED")
                }}</span>
              </h1>
            </div>
            <div ref="desc__inner">
              <html-with-links
                v-if="!isRepo"
                tag="p"
                class="heading__text"
                :html="app.description"
              ></html-with-links>
              <p v-else class="heading__text">{{ app.description }}</p>
            </div>
            <a href="#" class="more" @click.prevent="isDescExpanded = true">{{
              $t("VIEW MORE")
            }}</a>
          </div>
        </div>
        <div class="section" v-if="areScreenshotsVisible">
          <h2 class="h2">
            {{ $t("SCREENSHOTS") }}
            <ul class="select" v-if="!Array.isArray(app.screenshots)">
              <li
                v-for="(value, key) in details.screenshots"
                class="select__tab"
                :class="{ select__tab_active: screenshotTab === key }"
                @click="screenshotTab = key"
                :key="key"
              >
                {{ beautifyDevice(key) }}
              </li>
            </ul>
          </h2>
          <ul
            v-if="Array.isArray(app.screenshots)"
            class="screenshots"
          >
            <li
              class="screenshots__item"
              v-for="(screenshot, idx) in app.screenshots"
              :key="idx"
            >
              <img class="screenshots__img" :src="screenshot" />
            </li>
          </ul>
          <ul
            v-else
            class="screenshots"
            v-for="(array, key) in details.screenshots"
            :key="key"
            v-show="screenshotTab === key"
          >
            <li
              class="screenshots__item"
              v-for="screenshot in array"
              :key="screenshot.src"
            >
              <img class="screenshots__img" :src="screenshot.src" />
            </li>
          </ul>
        </div>
      </div>
      <sidebar
        :linkToInstallError="linkToInstallError"
        :appStoreUrl="app.appStoreUrl"
        :isTweaked="isTweaked"
        :tweakedApps="tweakedApps"
        :type="type"
        :currentVersion="app.version"
        :versions="app.versions"
        :id="details.id"
        :originalId="details.original_trackid"
        :originalType="details.original_section"
        :repo="isRepo"
        class="main__sidebar"
      />
      <div class="main__content main__content_b main__content_ptop">
        <div v-if="type !== 'books' && app.whatsnew" class="section">
          <h2 class="h2">{{ $t("WHATSNEW") }}</h2>
          <div
            ref="changelog"
            class="changelog"
            :class="{ changelog_expanded: isChangelogExpanded }"
          >
            <p ref="changelog__inner" v-html="app.whatsnew"></p>
            <a
              href="#"
              class="more"
              @click.prevent="isChangelogExpanded = true"
              >{{ $t("VIEW MORE") }}</a
            >
          </div>
        </div>
        <div class="section section_noborder">
          <h2 class="h2 h2_badge">
            {{ $t("INFORMATION") }}
            <span
              class="info-badge"
              v-if="details.compatibility_string"
            >{{ details.compatibility_string }}</span>
          </h2>
          <div class="info">
            <ul class="info__list">
              <li class="info__item" v-if="app.developer.name">
                <span class="info__intro">{{ $t("DEVELOPER") }}:</span>
                <router-link
                  v-if="app.developer.id"
                  class="info__link"
                  :to="`/search/?type=${type}&dev=${app.developer.id}`"
                  >{{ app.developer.name }}</router-link
                >
                <span v-else>{{ app.developer.name }}</span>
              </li>
              <li class="info__item" v-if="app.website">
                <span class="info__intro">{{ $t("WEBSITE") }}:</span>
                <a class="info__link" :href="app.website" target="_blank">{{
                  $t("VISIT")
                }}</a>
              </li>
              <li class="info__item" v-if="details.price">
                <span class="info__intro">{{ $t("PRICE") }}:</span>
                {{ $t((details.price || "").toUpperCase()) }}
              </li>
              <li
                class="info__item"
                v-if="genres[type] && genres[type][details.genre_id]"
              >
                <span class="info__intro">{{ $t("CATEGORY") }}:</span>
                <router-link
                  class="info__link"
                  :to="`/search/?type=${type}&genre=${details.genre_id}`"
                >
                  {{
                    $t((genres[type][details.genre_id].name || "").toUpperCase())
                  }}</router-link
                >
              </li>
              <li
                class="info__item"
                v-if="this.details.last_parse_itunes.customer_rating"
              >
                <span class="info__intro">{{ $t("CUSTOMER RATINGS") }}:</span>
                {{ this.details.last_parse_itunes.customer_rating }}
              </li>
              <li
                class="info__item"
                v-if="
                  type !== 'books' && app.updatedAt
                "
              >
                <span class="info__intro">{{ $t("UPDATED") }}:</span>
                {{ app.updatedAt }}
              </li>
              <li
                class="info__item"
                v-if="type !== 'books' && app.version"
              >
                <span class="info__intro">{{ $t("VERSION") }}:</span>
                {{ app.version }}
              </li>
              <li class="info__item" v-if="app.size">
                <span class="info__intro">{{ $t("SIZE") }}:</span>
                {{ app.size }}
              </li>
              <li
                class="info__item"
                v-if="this.details.last_parse_itunes.languages"
              >
                <span class="info__intro">{{ $t("LANGUAGE") }}:</span>
                {{ this.details.last_parse_itunes.languages }}
              </li>
              <li class="info__item" v-if="this.details.last_parse_itunes.seller">
                <span class="info__intro">{{ $t("SELLER") }}:</span>
                {{ this.details.last_parse_itunes.seller }}
              </li>
              <li
                class="info__item"
                v-if="this.details.last_parse_itunes.publisher"
              >
                <span class="info__intro">{{ $t("PUBLISHER") }}:</span>
                {{ this.details.last_parse_itunes.publisher }}
              </li>
            </ul>
            <ul class="info__downloads" v-if="!isRepo">
              <li class="info__downloads-heading">{{ $t("DOWNLOADS") }}</li>
              <li class="info__download">
                {{ details.clicks_day }} {{ $t("TODAY") }}
              </li>
              <li class="info__download">
                {{ details.clicks_week }} {{ $t("THIS WEEK") }}
              </li>
              <li class="info__download">
                {{ details.clicks_month }} {{ $t("THIS MONTH") }}
              </li>
              <li class="info__download">
                {{ details.clicks_year }} {{ $t("THIS YEAR") }}
              </li>
              <li class="info__downloads-total">
                {{ details.clicks_all }} {{ $t("TOTAL") }}
              </li>
            </ul>
          </div>
        </div>
      </div>
    </template>
  </section>
</template>

<script>
import prettysize from 'prettysize';
import { sanitizeUrl } from '@braintree/sanitize-url';
import Sidebar from '@/components/AppSidebar/index.vue';
import Loader from '@/components/Loader';
import HtmlWithLinks from '@/components/HtmlWithLinks';
import BackLink from '@/components/BackLink';
import { mapGetters } from 'vuex';
import axios from 'axios';

export default {
  components: {
    Sidebar, Loader, HtmlWithLinks, BackLink,
  },
  data() {
    return {
      requestCounter: 0,
      isDescExpanded: false,
      screenshotTab: 'iphone',
      isChangelogExpanded: false,
      details: {
        image: '',
        last_parse_itunes: {
          artist: {},
          raw_parse_itunes: {},
        },
      },
      repoName: '',
      appFromRepo: {},
      versions: {},
      isLoaded: false,
      prevRoute: null,
      linkToInstallError: false,
    };
  },
  computed: {
    ...mapGetters(['isDeviceLinked']),
    title() {
      return this.app.name;
    },
    repoId() {
      return this.$route.params.repoId;
    },
    isRepo() {
      return Boolean(this.repoId);
    },
    id() {
      return this.$route.params.id;
    },
    isTweaked() {
      return Boolean(this.details.original_trackid) && this.details.original_trackid !== '0';
    },
    tweakedApps() {
      const versions = this.details.tweaked_versions;
      if (!versions) {
        return [];
      }

      const apps = [];
      Object.keys(versions).forEach((type) => {
        apps.push(...versions[type].map(app => ({ type, id: app.trackid, name: app.name })));
      });

      return apps;
    },
    genres() {
      return this.$store.getters.genres;
    },
    type() {
      if (this.isRepo) {
        return 'altstore';
      }

      return this.$route.params.type;
    },
    image() {
      let image = '';

      if (this.isRepo) {
        return this.appFromRepo.iconURL;
      }

      try {
        image = this.details.last_parse_itunes.raw_parse_itunes.artworkUrl512;
      } catch {
        // eslint-disable-next-line prefer-destructuring
        image = this.details.image;
      }
      return image || this.details.image;
    },
    areScreenshotsVisible() {
      if (!this.app.screenshots) {
        return false;
      }

      if (Array.isArray(this.app.screenshots)) {
        return this.app.screenshots.length;
      }

      if (typeof this.app.screenshots === 'object') {
        return Object.keys(this.app.screenshots).length;
      }

      return false;
    },
    githubLink() {
      if (!this.isRepo) {
        return '';
      }

      const owner = this.appFromRepo.githubOwner;
      const repository = this.appFromRepo.githubRepository;

      if (!owner || !repository) {
        return '';
      }

      return sanitizeUrl(`https://github.com/${owner}/${repository}`);
    },
    app() {
      if (this.isRepo) {
        const app = {
          name: this.appFromRepo.name,
          image: this.appFromRepo.iconURL,
          description: this.appFromRepo.localizedDescription,
          screenshots: this.appFromRepo.screenshotURLs,
          bundle_id: this.appFromRepo.bundleIdentifier,
          developer: {
            name: this.appFromRepo.developerName,
          },
          version: this.appFromRepo.version,
          updatedAt:(this.appFromRepo.versionDate
            ? new Date(this.appFromRepo.versionDate).toLocaleDateString() : null),
          size: (this.appFromRepo.size ? prettysize(this.appFromRepo.size) : null),
          website: this.githubLink,
          versions: {},
          appStoreUrl: null,
        };

        if (this.appFromRepo.downloadURL) {
          app.versions[this.appFromRepo.version] = [
            {
              name: this.appFromRepo.name,
              id: this.appFromRepo.bundleIdentifier,
              link: this.appFromRepo.downloadURL,
              host: this.getHost(this.appFromRepo.downloadURL),
              image: this.appFromRepo.iconURL,
            },
          ];
        } else if (this.appFromRepo.versions) {
          this.appFromRepo.versions.forEach((versionData) => {
            if (!app.updatedAt) {
              app.updatedAt = new Date(versionData.date).toLocaleDateString();
            }
            if (!app.size) {
              app.size = prettysize(versionData.size);
            }

            app.versions[versionData.version] = [
              {
                name: this.appFromRepo.name,
                id: this.appFromRepo.bundleIdentifier,
                link: versionData.downloadURL,
                host: this.getHost(versionData.downloadURL),
                image: this.appFromRepo.iconURL,
              },
            ];
          });
        }

        return app;
      }

      return {
        name: this.details.name,
        image: this.image,
        description: this.details.description,
        screenshots: this.details.screenshots,
        whatsnew: this.details.whatsnew,
        developer: {
          name: this.details.pname,
          id: +this.details.artist_id,
        },
        version: this.details.version,
        updatedAt: this.details.last_parse_itunes.published,
        size: this.details.last_parse_itunes.size,
        website: this.details.pwebsite,
        appStoreUrl: this.details.appstore_url,
        versions: this.versions,
      };
    },
  },
  methods: {
    beautifyDevice(device) {
      const dict = {
        iphone: 'iPhone',
        ipad: 'iPad',
        appletv: 'Apple TV',
        osx: 'macOS',
      };

      return dict[device] || device;
    },
    getHost(link) {
      try {
        const url = new URL(link);
        return url.host || url.hostname;
      } catch {
        return link;
      }
    },
    async handleTriggers() {
      await this.$nextTick();

      const descWrapper = this.$refs.desc && this.$refs.desc.getBoundingClientRect().bottom;
      const descText = this.$refs.desc__inner
        && this.$refs.desc__inner.getBoundingClientRect().bottom;

      if (descText <= descWrapper) {
        this.isDescExpanded = true;
      }

      if (this.$refs.changelog__inner
        && this.$refs.changelog__inner.getBoundingClientRect().height <= 85) {
        this.isChangelogExpanded = true;
      }
    },
    handle18PlusContent(data) {
      // eslint-disable-next-line camelcase
      if (data?.is_18plus !== 1) {
        return;
      }

      this.$confirm(this.$t('AGE_RESTRICTED_CONTENT_ALERT'), {
        hidePage: true,
        closeManually: true,
        okText: 'GO BACK',
        cancelText: 'IM_18_PLUS',
      }).then(() => {
        this.$router.push(this.prevRoute?.fullPath || '/');
      }).finally(() => {
        this.$modal.hide('dialog');
      });
    },
    goTo404() {
      this.$router.replace({
        name: 'error',
        params: {
          message: '404_CONTENT_MESSAGE',
          link: '/',
          linkText: 'GO_TO_MAIN_PAGE',
        },
      });
    },
    goToRepo() {
      this.$router.replace({
        name: 'error',
        params: {
          message: 'NO_APP_IN_REPO_ERROR',
          link: '/my/repos',
          linkText: 'MY_ALTSTORE_REPOSITORIES',
        },
      });
    },
    async getInfo() {
      this.isLoaded = false;

      if (this.isRepo) {
        try {
          const response = await this.$api.getAltstoreRepo(this.repoId);
          if (!response.success) {
            throw response;
          }

          this.repoName = response.data[0].name;

          const contentsResponse = await axios.get(response.data[0].contents_uri);
          const app = contentsResponse.data.apps[this.id];


          if (!app) {
            this.goToRepo();
            return;
          }

          this.appFromRepo = app;
        } catch (response) {
          this.$router.replace({
            name: 'error',
            params: {
              message: response.errors[0].translated,
              link: '/my/repos',
              linkText: 'MY_ALTSTORE_REPOSITORIES',
            },
          });
        } finally {
          this.isLoaded = true;
        }

        return;
      }

      const data = await this.$api.getAppDetails({
        type: this.type,
        trackid: this.id,
      });

      if (!data.length) {
        this.goTo404();
        return;
      }

      this.details = { ...data[0] };

      this.handle18PlusContent(this.details);

      try {
        this.details.screenshots = JSON.parse(this.details.screenshots);
        // eslint-disable-next-line
        this.screenshotTab = Object.keys(this.details.screenshots)[0];
      } catch {
        this.details.screenshots = null;
      }

      try {
        this.details.last_parse_itunes = JSON.parse(this.details.last_parse_itunes) || {
          artist: {},
          raw_parse_itunes: {},
        };
      } catch {
        this.details.last_parse_itunes = {
          artist: {},
          raw_parse_itunes: {},
        };
      }

      try {
        const response = await this.$api.getLinks({
          type: this.type,
          trackid: this.id,
        });

        this.versions = (response.data && response.data[this.id]) || {};

        this.linkToInstallError = response.errors.some(error => error.code === 'ERROR_NO_DEVICE_LINKED');
      } finally {
        this.isLoaded = true;
      }
    },
  },
  watch: {
    isLoaded(newVal) {
      if (newVal) {
        this.handleTriggers();
      }
    },
    // eslint-disable-next-line
    '$route.params.id': function () {
      this.getInfo();
    },
  },
  serverPrefetch() {
    return this.getInfo();
  },
  created() {
    this.getInfo();
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      // eslint-disable-next-line no-param-reassign
      vm.prevRoute = from;
    });
  },
};
</script>

