<template>
  <div class="version version_m">
    <h4 class="version__title" v-if="version[0] != '0'">v {{ version[0] }}</h4>
    <div
      class="version__item"
      :class="{'version__item_center': repo}"
      v-for="item in version[1]"
      :key="item.id"
    >
      <div
        class="version__install-wrapper"
        v-if="repo || item.di_compatible && canBeInstalled"
      >
        <a
          :class="{ version__install_pending: item.id === idPending }"
          @click.prevent="install(item.id)"
          class="version__install"
          href="#"
        >
          <img class="version__install-icon" src="@/assets/icon-install.svg" />
          {{ $t("INSTALL") }}
          <template v-if="item.id === idPending">
            <div class="version__loader">
              <div class="version__loader-dot"></div>
              <div class="version__loader-dot"></div>
              <div class="version__loader-dot"></div>
            </div>
            <div class="version__tooltip">{{ $t("REQUESTING") }}...</div>
          </template>
        </a>
      </div>

      <div class="version__desc">
        <div class="version__row">
          <a
            v-if="repo"
            class="version__link"
            :class="{
              version__link_verified: item.verified,
            }"
            :href="item.link"
            target="_blank"
          >
            {{ item.host }}
            <img class="version__open" src="@/assets/icon-open.svg" />
          </a>
          <a
            v-else
            class="version__link"
            :class="{
              version__link_disabled: getLinkDescription(item.link).disabled,
              version__link_verified: item.verified,
            }"
            @click.prevent="open(item.link, item.host)"
          >
            {{ item.host }}
            <img class="version__open" src="@/assets/icon-open.svg" />
          </a>
          <a
            v-if="!repo && !item.protected"
            @click.prevent="reportLink(item.id)"
            href="#"
            class="version__report"
            >{{ $t("REPORT_LINK_BUTTON") }}</a
          >
        </div>

        <span v-if="item.provider || item.uploader_name" class="version__text">
          <template v-if="item.provider">
            {{ $t("PROVIDER") }}: {{ item.provider }} • </template
          ><template v-if="item.uploader_name">
            {{ $t("UPLOADER") }}: {{ item.uploader_name }}
          </template>
        </span>
        <div v-if="item.reports && item.reports.length" class="version__warning">
          <span class="version__warning-icon">
            <img src="@/assets/icon-warning.svg" />
          </span>
          <span v-html="$t('LINK_REPORTED', { reason: item.reports[0].reason })"></span>
        </div>
        <html-with-links
          v-if="item.is_compatible"
          class="version__badge"
          :class="{
            version__badge_warning: item.is_compatible.result === 'unknown',
            version__badge_success: item.is_compatible.result === 'yes',
            version__badge_error: item.is_compatible.result === 'no',
          }"
          :html="
            getCompatibleMessage(
              item.is_compatible.reason,
              item.is_compatible.result
            )
          "
        ></html-with-links>
      </div>
    </div>
  </div>
</template>

<script>
import installAppMixin from '@/mixins/installApp';
import HtmlWithLinks from '@/components/HtmlWithLinks';

export default {
  mixins: [installAppMixin],
  components: {
    HtmlWithLinks,
  },
  props: {
    type: String,
    version: Array,
    idPending: [Number, String],
    repo: Boolean,
  },
  computed: {
    canBeInstalled() {
      return this.type === 'ios' || this.type === 'cydia' || this.type === 'tvos' || this.type === 'books';
    },
  },
  methods: {
    async open(link, host) {
      const isOnion = /\.onion$/i.test(host);

      if (isOnion) {
        await this.$confirm(this.$t('TOR_LINK_ERROR'));
      }

      const desc = this.getLinkDescription(link);

      if (desc.href) {
        // eslint-disable-next-line no-restricted-globals
        location.href = desc.href;
        return;
      }

      if (desc.ticket) {
        this.$router.push(`/redirect/${desc.ticket}`);
      }
    },
    getLinkDescription(link) {
      if (link === '@') {
        return { disabled: true };
      }

      try {
        const url = new URL(link);

        if (url.protocol === 'ticket:') {
          return { ticket: url.pathname.slice(2) || url.host };
        }

        return { href: link };
      } catch {
        return { disabled: true };
      }
    },
    async install(id) {
      if (this.idPending) {
        return;
      }

      this.$emit('install', id);

      if (!this.repo) {
        this.installApp(id, this.$route.params.type || this.type).finally(() => { this.$emit('install', 0); });
        return;
      }

      const version = this.version[1].find(v => v.id === id);

      if (typeof (id) === 'undefined') {
        this.$notify({
          type: 'error',
          text: this.$t('ERROR_REPO_BUNDLE_ID_IS_MISSING'),
        });
        return;
      }


      this.installApp(id, this.type, {
        isFromRepo: true,
        name: version.name,
        link: version.link,
        image: version.image,
      }).finally(() => { this.$emit('install', 0); });
    },
    reportLink(id) {
      if (this.checkLinking(this.$t('LINK_TO_REPORT'))) {
        return;
      }

      this.$prompt(this.$t('REPORT_NOTICE', { href: 'https://telegra.ph/App-installation-troubleshooting-03-20' })).then(reason => this.$api.reportLink({
        type: this.$route.params.type,
        id,
        reason,
      })).then(() => {
        this.$notify({
          type: 'success',
          text: this.$t('REPORT_SUBMITTED'),
        });
      });
    },
    checkLinking(error) {
      if (!this.$store.getters.isDeviceLinked) {
        this.$confirm(error).then(() => {
          this.$router.push('/link');
        });
      }

      return !this.$store.getters.isDeviceLinked;
    },
    getCompatibleMessage(reason, status) {
      if (reason) {
        return reason;
      }

      if (status === 'yes') {
        return this.$t('COMPATIBLE_WITH_DEVICE');
      }

      if (status === 'no') {
        return this.$t('NOT_COMPATIBLE_WITH_DEVICE');
      }

      return this.$t('COMPATIBILITY_UNKNOWN');
    },
  },
};
</script>

<style lang="scss" scoped>
